.btn-queue, .btn-watch, .modal__btn--right, .modal__btn--left {
  width: 110px;
  height: 44px;
  cursor: pointer;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid;
  border-radius: 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  transition-property: background-color, color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.btn-queue:hover, .btn-watch:hover, .modal__btn--right:hover, .modal__btn--left:hover {
  background-color: var(--cl-accent);
  color: var(--cl-white);
}

.modal-backdrop {
  z-index: 2000;
  width: 100%;
  height: 100%;
  opacity: 1;
  background: rgba(0, 0, 0, .2);
  transition-property: opacity;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.modal-backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

